import React from "react";
import "./About.scss";
import PageTitle from "../../common/PageTitle/PageTitle";

const aboutDB = {
  title: "poznaj moją historię",
  desc: [
    "Nazywam się arkadiusz zygan. Około rok temu postanowiłem usystematyzować wiedzę z zakresu kodowania i programowania. Naprzestrzeni lat interesowałem się nowymi technologiami, jednak nigdy nie uzyskałem pełnej wiedzy teoretycznej, jak i nie zrealizowałem praktycznych projektów. Od roku systematycznie uczę się programowania po stronie front-endu.",
    "Ukończyłem intensywny 9-miesięczny kurs programowania pod opieką indywidualnego mentora – Bootcamp Web Developer Plus w Kodilla. Uczestniczyłem również w szkoleniach rozszerzających moje umiejętnośći na platformie Udemy z zakresu JavaScript, React i Data science.",
    "/'praktyka czyni mistrza/' - każdy z nas to słyszał chociaż raz w swoim życiu. zgadzam się, ale uważam, że trzeba dodać 'świadome ćwiczenie' - termin ten wykuł Anders Erics­son.  Uważa on również, że aby osiągnąć wysoki poziom w dowolnej dziedzinie potrzeba nawet 10000 godzin. Zdaję sobię sprawę, że wciąż dużo pracy przede mną. Ćwiczę i rozwijam swoje umiejętności w JavaScript, React, HTML5 oraz CSS/Sass. Wiem, że jestm na początku tej drogi, ale jestem zmotywowany, aby stawiać kolejne kroki.",
  ],
};

const About = () => {
  const textElem = aboutDB.desc.map((item, index) => (
    <div key={index} className='aboutText'>
      {item}
    </div>
  ));

  return (
    <div className='pageContent'>
      <div className='about'>
        <PageTitle>{aboutDB.title}</PageTitle>
        {textElem}
      </div>
    </div>
  );
};

export default About;
