import React from "react";
import "./Contact.scss";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGithub, faLinkedin } from "@fortawesome/free-brands-svg-icons";
import { faEnvelope, faFilePdf } from "@fortawesome/free-solid-svg-icons";
import PageTitle from "../../common/PageTitle/PageTitle";

import Plpin from "./pin_pl.png";
import Ukpin from "./pin_uk.png";

const contactDB = {
  title: "Napisz do mnie",
  content: {
    lin: {
      title: "/in/arkadiusz-zygan",
      link: "https://www.linkedin.com/in/arkadiusz-zygan/",
      icon: faLinkedin,
      class: "in",
    },
    git: {
      title: "github.com/Zyar3k",
      link: "https://github.com/Zyar3k",
      icon: faGithub,
      class: "git",
    },
    mail: {
      title: "a.zygan@gmail.com",
      link: "mailto:a.zygan@gmail.com",
      icon: faEnvelope,
      class: "mail",
    },
  },
};

const Contact = () => {
  return (
    <div className='pageContent'>
      <PageTitle>{contactDB.title}</PageTitle>
      <div className='contact'>
        <div className='boxWrapper'>
          <div className='contactBox'>
            <div className='pinWrapper'>
              <a
                href='https://drive.google.com/file/d/1d2C9pub8Kvj9UCHmJDoG0_JCUtyKupmu/view'
                target='_blank'
                rel='noopener noreferrer'
              >
                <img src={Plpin} alt='pin' className='pin' />
              </a>
              <div>
                <FontAwesomeIcon className='icon cv' icon={faFilePdf} />
              </div>
              <a
                href='https://drive.google.com/open?id=1sixNne4mGrT94aVRdj6vGBRf9OMkvZHp'
                target='_blank'
                rel='noopener noreferrer'
              >
                <img src={Ukpin} alt='pin' className='pin' />
              </a>
            </div>
            <div className='contactTitle'>Curriculum Vitae</div>
          </div>
          <a
            href='https://www.linkedin.com/in/arkadiusz-zygan/'
            target='_blank'
            rel='noopener noreferrer'
            className='contactBox'
          >
            <FontAwesomeIcon
              className={`icon ${contactDB.content.lin.class}`}
              icon={contactDB.content.lin.icon}
            />
            <div className='contactTitle'>{contactDB.content.lin.title}</div>
          </a>
        </div>
        <div className='boxWrapper'>
          <a
            href={contactDB.content.git.link}
            target='_blank'
            rel='noopener noreferrer'
            className='contactBox'
          >
            <FontAwesomeIcon
              className={`icon ${contactDB.content.git.class}`}
              icon={contactDB.content.git.icon}
            />
            <div className='contactTitle'>{contactDB.content.git.title}</div>
          </a>
          <a
            href={contactDB.content.mail.link}
            target='_blank'
            rel='noopener noreferrer'
            className='contactBox'
          >
            <FontAwesomeIcon
              className={`icon ${contactDB.content.mail.class}`}
              icon={contactDB.content.mail.icon}
            />
            <div className='contactTitle'>{contactDB.content.mail.title}</div>
          </a>
        </div>
      </div>
    </div>
  );
};

export default Contact;
